.Briefcase__row {
  margin: 8px -16px;
}

/* Add padding BETWEEN each column (if you want) */
.Briefcase__row,
.Briefcase__row > .Briefcase__column {
  padding: 3px;
  text-align: center;
}

/* Create three equal Briefcase__columns that floats next to each other */
.Briefcase__column {
  /* float: left; */
  display: inline-block;

  width: 44%;
  /* display: none; Hide columns by default */
}

/* Clear floats after rows */
.Briefcase__row:after {
  content: "";
  display: table;
  clear: both;
}

/* Content */
/* .content { */

/* } */

/* The "show" class is added to the filtered elements */
.show {
  display: block;
}

.Briefcase__cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  grid-gap: 2rem;
  grid-gap: 2rem;
  gap: 2rem;
  margin: 1rem 5vw;
  padding: 0;
  list-style-type: none;
}

